import * as React from 'react';
import { useState } from 'react';
import { Wrapper } from '../src/components/Container';
import Home from '../src/models';
interface IProps {
    title: string;
    symbol: string
}
export default ({ title, symbol }: IProps) => {
    const [communityName, setcommunityName] = useState(title);
    const [communitySymbol, setcommunitySymbol] = useState(symbol);

    console.log('HOME PROPS', title);
    console.log('HOME PROPS', symbol);

    return (
        <Wrapper>
            <Home
                setcommunityName={setcommunityName}
                setcommunitySymbol={setcommunitySymbol}
                title={title}
                symbol={symbol}
            />
        </Wrapper>
    );
}