import * as React from 'react';
import { Fragment, useState } from 'react';
// import { useCreateCommunity } from '../assets/helper/index';
import Box from '../components/Box';
import Form from '../components/Form';

interface IProps {
    title: any;
    symbol: any
    setcommunitySymbol: any;
    setcommunityName: any;
}

export default ({ title, symbol, setcommunityName, setcommunitySymbol }: IProps) => {
    const [boarding, setOnboarding] = useState(1);

    return (
        <Fragment>
            {
                boarding === 1 && (
                    <Fragment>
                        <Box title="Create a community name">
                            <Form
                                title='Enter community name'
                                onSubmit={(text: any) => {
                                    setcommunityName(text)
                                    setOnboarding(2)
                                }}
                                onClick={() => console.log('hello')}
                            />
                        </Box>
                    </Fragment>
                )
            }

            {
                boarding === 2 && (
                    <Fragment>
                        <Box title="Create a community Symbol">
                            <Form
                                title='Enter community Symbol'
                                onSubmit={(text: any) => {
                                    setcommunitySymbol(text)
                                    setOnboarding(3)
                                }}
                                onClick={() => console.log('hello')}
                            />
                        </Box>
                    </Fragment>
                )
            }

            {
                boarding === 3 &&
                (
                    <Fragment>
                        <Box title={`Community Name: ${title}`} >
                            <h3 style={{ color: 'black' }} >
                                Community Symbol: {symbol}
                            </h3>
                        </Box>
                    </Fragment>
                )
            }
        </Fragment>
    )
}